@font-face {
  font-family: "m-regular";
  src: url("../fonts/Montserrat-Regular.ttf");
}

@font-face {
  font-family: "m-bold";
  src: url("../fonts/Montserrat-Bold.ttf");
}

@font-face {
  font-family: "m-medium";
  src: url("../fonts/Montserrat-Medium.ttf");
}

@font-face {
  font-family: "m-light";
  src: url("../fonts/Montserrat-Light.ttf");
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "m-regular", sans-serif;
  text-decoration: none;
}

::selection {
  background-color: rgb(44, 171, 255);
  color: #000;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("../images/code_bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  background-blend-mode: darken;
  background-color: rgba(22, 22, 22, 0.8235294118);
  color: #ffffff;
}

.card {
  display: flex;
  gap: 0 20px;
  background-color: rgba(0, 0, 0, 0.555);
}

.card .myImg {
  max-width: 240px;
  border-right: 1px solid rgba(201, 201, 201, 0.1725490196);
  padding: 10px;
}

.card .myImg img {
  width: 100%;
  border-radius: 50%;
  animation: colorAnim alternate 0.8s infinite;
  padding: 4px;
}

@keyframes colorAnim {
  0% {
    border: 1.5px solid rgba(0, 128, 0, 0);
  }

  100% {
    border: 1.5px solid rgb(44, 171, 255);
  }
}

.card .myImg .social_icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px 15px;
  max-width: 150px;
  margin: 40px auto;
}

.card .myImg .social_icons a {
  max-width: 40px;
  transition: 0.8s ease;
}

.card .myImg .social_icons a img {
  width: 100%;
}

.card .myImg .avInfo_web {
  max-width: 130px;
  margin: 0 auto;
}

.card .myImg .avInfo_web a:hover path {
  fill: rgb(44, 171, 255);
  transition: 0.2s ease;
}

.card .my__info_container {
  padding: 20px;
}

.card .my__info_container .myName .name {
  font-size: min(40px, 10vw);
  letter-spacing: 3px;
  text-shadow: 2px 2px 3px rgb(0, 0, 0);
}

.card .my__info_container .myName .prof {
  margin-top: 10px;
  letter-spacing: 1.5px;
  font-size: 16px;
  text-shadow: 2px 2px 3px rgb(0, 0, 0);
  font-family: "m-medium";
}

.card .my__info_container .myInfo_title {
  margin-top: 30px;
  font-size: 20px;
}

.card .my__info_container .myInfo_title:nth-child(2) {
  margin-top: 55px;
}

.card .my__info_container .myInfo_text {
  max-width: 400px;
  padding-top: 25px;
  font-size: 17px;
  text-shadow: 2px 2px 3px rgb(0, 0, 0);
  line-height: 26px;
  letter-spacing: 0.6px;
}

.card .my__info_container .myMail {
  display: flex;
  align-items: center;
  text-shadow: 2px 2px 3px rgb(0, 0, 0);
  gap: 0 10px;
  margin-top: 30px;
}

.card .my__info_container .myMail .mail_icon img {
  width: 25px;
}

.card .my__info_container .myMail .mail {
  letter-spacing: 1px;
  font-size: min(17px, 4vw);
}

.card .my__info_container .myPhone {
  display: flex;
  align-items: center;
  text-shadow: 2px 2px 3px rgb(0, 0, 0);
  gap: 0 10px;
  margin-top: 10px;
}

@media screen and (max-width: 600px) {
  .card {
    flex-direction: column;
  }

  .card .myImg {
    border-right: none;
    max-width: 280px;
    margin: 0 auto;
  }

  .card .myImg .social_icons {
    flex-wrap: nowrap;
    max-width: 270px;
    align-items: center;
  }
}

@media screen and (max-width: 420px) {
  .card .my__info_container .myMail .mail_icon img {
    width: 20px;
  }

  .card .my__info_container .myMail .mail {
    font-size: min(15px, 4vw);
  }
}